}
.headerNavTop>.div2 .open .p1>span,.headerNavTop>.div2 .open .nav-hamburger span{
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
  opacity: 0;
}
.nav-cross{
  position: absolute;
  height: 50px;
  width: 50px;
  -webkit-transform: rotate(45deg) scale(.7);
          transform: rotate(45deg) scale(.7);
  right: 0;
  top: -10px;
}
.nav-cross:before {
  height: 0;
  width: 2px;
  top: 0;
  left: 45%;
}
.nav-cross:after {
  width: 0;
  height: 2px;
  left: 0;
  top: 45%;
}
.nav-cross:before, .nav-cross:after{
  position: absolute;
  content: "";
  background: #FFFFFF;
  -webkit-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}
.headerNavTop>.div2 .open .nav-cross:before{
  height: 100%;
  -webkit-transition-delay: .625s;
          transition-delay: .625s;
}
.headerNavTop>.div2 .open .nav-cross:after{
  width: 100%;
  -webkit-transition-delay: .375s;
          transition-delay: .375s;
}
